import React, { Component } from "react";
import Image from "../../Image8.png";
import Button from "./Button";
import InputField from "./InputField";
import OuterNavbar from "./OuterNavbar";
import ScrollToTop from "./ScrollToTop";
import Footer from "./Footer";
import emailjs from "emailjs-com";
import TextField from "@material-ui/core/TextField";

export class Contact extends Component {
  constructor(props) {
    super(props);
    this.state = {
      fname: "",
      lname: "",
      query: ""
    };
  }
  submitForm(e) {
    e.preventDefault();
    var data = this.state;

    emailjs
      .send("gmail", "template_X1Jcd1qy", data, "user_20gNMQK3hs5jQXTFuKYUg")
      .then(
        result => {
          console.log(result.text);
        },
        error => {
          console.log(error.text);
        }
      );
    this.props.history.push("/message-sent");
  }
  handleChange = (param, e) => {
    this.setState({ [param]: e.target.value });
  };
  render() {
    return (
      <div>
        <ScrollToTop />
        <OuterNavbar />
        <div className="wrapper">
          <div className="heading p-5">
            <h2 className="text-center">Got a question? send it to us</h2>
          </div>
          <div
            className="contactus mt-5"
            style={{
              backgroundColor: "#f36f21eb",
              height: "75.5vh"
            }}
          >
            <div className="hide">
              <div className="container d-flex justify-content-center pt-3">
                <img
                  src={Image}
                  className="img-fluid mobile-plane"
                  alt="illustration"
                />
              </div>
              <form onSubmit={this.submitForm.bind(this)}>
                <div className="container p-3 boxing">
                  <div class="form-group">
                    <TextField
                      ype="text"
                      label="first Name"
                      id="fname"
                      required
                      onChange={this.handleChange.bind(this, "fname")}
                      fullWidth
                    />
                  </div>
                  <div class="form-group">
                    <TextField
                      type="text"
                      label="Last Name"
                      id="lname"
                      required
                      onChange={this.handleChange.bind(this, "lname")}
                      fullWidth
                    />
                  </div>
                  <div class="form-group">
                    <TextField
                      type="text"
                      label="Your Query"
                      id="query"
                      required
                      onChange={this.handleChange.bind(this, "query")}
                      fullWidth
                    />
                  </div>
                  <div className="container d-flex justify-content-center p-3">
                    <Button line={"Send"} type={"submit"} />
                  </div>
                </div>
              </form>
            </div>
            <div
              className="contact2"
              style={{
                backgroundColor: "#ffffff",
                height: "75vh"
              }}
            >
              <div className="container d-flex justify-content-center pt-3">
                <img
                  src={Image}
                  className="img-fluid contact-plane"
                  alt="illustration"
                />
              </div>
              <form onSubmit={this.submitForm.bind(this)}>
                <div className="container p-3">
                  <div class="form-group">
                    <TextField
                      ype="text"
                      label="first Name"
                      id="fname"
                      required
                      onChange={this.handleChange.bind(this, "fname")}
                      fullWidth
                    />
                  </div>
                  <div class="form-group">
                    <TextField
                      type="text"
                      label="Last Name"
                      id="lname"
                      required
                      onChange={this.handleChange.bind(this, "lname")}
                      fullWidth
                    />
                  </div>
                  <div class="form-group">
                    <TextField
                      type="text"
                      label="Your Query"
                      id="query"
                      required
                      onChange={this.handleChange.bind(this, "query")}
                      fullWidth
                    />
                  </div>
                  <div className="container d-flex justify-content-center p-3">
                    <Button line={"Send"} type={"submit"} />
                  </div>
                </div>
              </form>
            </div>
            <div
              className="contact3"
              style={{
                backgroundColor: "#f36f21",
                height: "75vh"
              }}
            ></div>
          </div>
        </div>
        <Footer />
      </div>
    );
  }
}

export default Contact;
