import React, { useContext } from "react";
import Image4 from "../../Image4.png";
import OuterNavbar from "../layout/OuterNavbar";
import Footer from "../layout/Footer";
import TextField from "@material-ui/core/TextField";
import { useState } from "react";
import { auth } from "./Firebase";
import { generateUserDocument } from "./Firebase";
import { UserContext } from "../../provider/UserProvider";
import { withRouter, Redirect, Link } from "react-router-dom";
function SignUp() {
  const { user } = useContext(UserContext);
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [displayName, setDisplayName] = useState("");
  const [error, setError] = useState(null);
  console.log(user);

  const createUserWithEmailAndPasswordHandler = async (
    event,
    email,
    password
  ) => {
    event.preventDefault();

    try {
      const { user } = await auth.createUserWithEmailAndPassword(
        email,
        password
      );

      generateUserDocument(user, { displayName });
      console.log(user);
    } catch (error) {
      setError("Error Signing up with email and password");
    }

    setEmail("");
    setPassword("");
    setDisplayName("");
  };
  const signOut = () => {
    auth
      .signOut()
      .then(() => {
        console.log("Sign out succesfull");
      })
      .catch(() => {
        console.log("error happend");
      });
  };

  const onChangeHandler = event => {
    const { name, value } = event.currentTarget;
    if (name === "userEmail") {
      setEmail(value);
    } else if (name === "userPassword") {
      setPassword(value);
    } else if (name === "displayName") {
      setDisplayName(value);
    }
  };
  return user ? (
    <div>
      <h3>
        You arr already logged in <br />
      </h3>
      <span className="btn btn-primary">
        <Link to="/">Back</Link>
      </span>
    </div>
  ) : (
    <div>
      <OuterNavbar />
      <div className="container dabba">
        <div className="row px">
          <div className="col-md-12 ">
            <div className="container Signuphoto">
              <img
                src={Image4}
                className="img-fluid image"
                alt="illustration"
                style={{ maxHeight: "300px" }}
              />
            </div>
          </div>
        </div>
        <div className="row padding">
          <div className="container">
            <h2 className="sign-heading text-center">
              Welcome Back to Novus,Login to get a<br />
              beautiful website !
            </h2>
            <form>
              <div className="container mine">
                <div className="pb-2">
                  <TextField
                    id="email"
                    type="email"
                    label="Email"
                    name="userEmail"
                    onChange={event => onChangeHandler(event)}
                    fullWidth
                  />
                </div>
              </div>
              <div className="container mine">
                <div className="pb-2">
                  <TextField
                    id="password"
                    type="password"
                    label="Password"
                    name="userPassword"
                    onChange={event => onChangeHandler(event)}
                    fullWidth
                  />
                </div>
              </div>
              <div className="container d-flex justify-content-center pt-2">
                <button
                  className="btn"
                  style={{
                    borderRadius: "5px",
                    padding: "5px 22px 5px 22px"
                  }}
                  type={"submit"}
                  onClick={event => {
                    createUserWithEmailAndPasswordHandler(
                      event,
                      email,
                      password
                    );
                  }}
                >
                  Sign Up{" "}
                </button>
              </div>
            </form>
            <button line="Sign Up" onClick={signOut}>
              Signout
            </button>
          </div>
        </div>
      </div>
      <Footer />
    </div>
  );
}

export default SignUp;
