import React from "react";
import OuterNavbar from "./OuterNavbar";
import Button from "./Button";
import { withRouter } from "react-router-dom";
import ScrollToTop from "./ScrollToTop";
import emailjs from "emailjs-com";
class Details extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      name: "",
      typeOfWebsite: "",
      description: "",
      contactno: "",
      email: ""
    };
  }

  submitForm(e) {
    e.preventDefault();

    var data = this.state;

    emailjs
      .send("gmail", "template_qZzS0ZFz", data, "user_djaCT8dtB9zm4k5wQ0mGq")
      .then(
        result => {
          console.log(result.text);
        },
        error => {
          console.log(error.text);
        }
      );
    this.props.history.push("/thank-you");
  }
  handleChange = (param, e) => {
    this.setState({ [param]: e.target.value });
  };

  render() {
    return (
      <div>
        <ScrollToTop />
        <OuterNavbar />
        <section>
          <div className="container p-3">
            <div className="container pb-3">
              <h2>Tell us more about your requirements</h2>
            </div>
            <div className="container details p-5">
              <form onSubmit={this.submitForm.bind(this)}>
                <div class="form-group">
                  <input
                    type="text"
                    class="form-control"
                    id="formGroupExampleInput"
                    placeholder="Your Name"
                    required
                    onChange={this.handleChange.bind(this, "name")}
                  />
                </div>
                <div class="form-group">
                  <select
                    class="form-control disabled"
                    id="exampleFormControlSelect1"
                    required
                    onChange={this.handleChange.bind(this, "typeOfWebsite")}
                  >
                    <option value="disabled selected">
                      Type of website needed
                    </option>
                    <option value="Blog">Blog</option>
                    <option value="E-commerce">E-commerce</option>
                    <option value="Regular Landing Page for company">
                      Regular Landing Page for company
                    </option>
                    <option value="Other">Other</option>
                  </select>
                </div>
                <div class="form-group">
                  <textarea
                    class="form-control"
                    id="exampleFormControlTextarea1"
                    rows="2"
                    placeholder="Website description"
                    required
                    onChange={this.handleChange.bind(this, "description")}
                  ></textarea>
                </div>
                <div class="form-group">
                  <input
                    type="text"
                    class="form-control"
                    id="formGroupExampleInput"
                    placeholder="Conact No."
                    required
                    onChange={this.handleChange.bind(this, "contactno")}
                  />
                </div>
                <div class="form-group">
                  <input
                    type="email"
                    class="form-control"
                    id="exampleFormControlInput1"
                    placeholder="Email"
                    required
                    onChange={this.handleChange.bind(this, "email")}
                  />
                </div>
                <div className="container d-flex justify-content-center p-3">
                  <Button line="submit" type={"submit"} />
                </div>
              </form>
            </div>
          </div>
        </section>
      </div>
    );
  }
}

export default withRouter(Details);
